    <div class="payment-box">
      <h2 class="payment__h2 text-center">
        Платежи. Оплата банковской картой онлайн.
      </h2>
      <p class="text-start">
        Наш сайт подключен к интернет-эквайрингу, и Вы можете оплатить Услугу
        банковской картой Visa или Mastercard. После подтверждения выбранного
        Товара либо услуги откроется защищенное окно с платежной страницей АО
        ForteBank, где Вам необходимо ввести данные Вашей банковской карты. Для
        дополнительной аутентификации держателя карты используется протокол 3-D
        Secure. Если Ваш Банк-эмитент поддерживает данную технологию, Вы будете
        перенаправлены на его сервер для прохождения дополнительной
        идентификации. Информацию о правилах и методах дополнительной
        идентификации уточняйте в Банке, выдавшем Вам банковскую карту.
      </p>
      <p>
        Услуга онлайн-оплаты осуществляется в соответствии с правилами
        Международных платежных систем Visa и MasterCard на принципах соблюдения
        конфиденциальности и безопасности совершения платежа, для этого
        используются самые актуальные методы проверки, шифрования и передачи
        данных по закрытым каналам связи. Ввод данных банковской карты
        осуществляется в защищенном окне на платежной странице АО ForteBank.
      </p>
      <div class="payment-flex">
        <p>
          В поля на платежной странице требуется ввести номер карты, имя
          владельца карты, срок действия карты, трёхзначный код безопасности
          (CVV2 для VISA или CVC2 для MasterCard). Все необходимые данные
          отображены на поверхности банковской карты.
        </p>
