      <p>
        Далее в том же окне откроется страница Вашего банка-эмитента для ввода
        3-D Secure кода. В случае, если у вас не настроен статичный 3-D Secure,
        он будет отправлен на ваш номер телефона посредством SMS. Если 3-D
        Secure код к Вам не пришел, то следует обратится в ваш банк-эмитент.
      </p>
      <p>
        3-D Secure — это самая современная технология обеспечения безопасности
        платежей по картам в сети интернет. Позволяет однозначно
        идентифицировать подлинность держателя карты, осуществляющего операцию,
        и максимально снизить риск мошеннических операций по карте.
      </p>
