    <div class="payment-box">
      <h2 class="payment__h2">Гарантия безопасности</h2>
      <p>
        АО ForteBank защищает и обрабатывает данные Вашей банковской карты по
        стандарту безопасности PCI DSS 3.0. Передача информации в платежный шлюз
        происходит с применением технологии шифрования SSL. Дальнейшая передача
        информации происходит по закрытым банковским сетям, имеющим наивысший
        уровень надежности. АО ForteBank не передает данные Вашей карты нам и
        иным третьим лицам. Для дополнительной аутентификации держателя карты
        используется протокол 3-D Secure. В случае, если у Вас есть вопросы по
        совершенному платежу, Вы можете обратиться в службу поддержки клиентов
        платежного сервиса по электронной почте
        <a href="mailto:support@cloudpayment.kz" class="payment-mail">
          support@cloudpayment.kz.</a
        >
      </p>
    </div>
    <div class="payment-box">
      <h2 class="payment__h2">Безопасность онлайн-платежей</h2>
      <p>
        Предоставляемая Вами персональная информация (имя, адрес, телефон,
        e-mail, номер кредитной карты) является конфиденциальной и не подлежит
        разглашению. Данные Вашей кредитной карты передаются только в
        зашифрованном виде и не сохраняются на нашем Web-сервере. Безопасность
        обработки Интернет-платежей гарантирует АО ForteBank. Все операции с
        платежными картами происходят в соответствии с требованиями VISA
        International, MasterCard и других платежных систем. При передаче
        информации используются специализированные технологии безопасности
        карточных онлайн-платежей, обработка данных ведется на безопасном
        высокотехнологичном сервере процессинговой компании. Оплата платежными
        картами безопасна, потому что: Система авторизации гарантирует
        покупателю, что платежные реквизиты его платежной карты (номер, срок
        действия, CVV2/CVC2) не попадут в руки мошенников, так как эти данные не
        хранятся на сервере авторизации и не могут быть похищены. Покупатель
        вводит свои платежные данные непосредственно в системе авторизации АО
        ForteBank, а не на сайте интернет-магазина, следовательно, платежные
        реквизиты карточки покупателя не будут доступны третьим лицам.
      </p>
